<template>
  <v-card flat max-width="100%">
    <v-card-text>
      <v-row align="center" justify="center">
        <v-col>
          <v-card class="mx-auto">
            <v-alert color="primary" dark icon="mdi-emoticon" border="left" prominent>
              <p></p>
              <p>
                Gravatar
                <br />
                {{ email }}Gravatar
              </p>
              <v-col class="shrink">
                <v-btn @click="go_gravatar()">Go Gravatar</v-btn>
              </v-col>
            </v-alert>
          </v-card>
        </v-col>

        <v-col>
          <v-card class="mx-4 pa-12">
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title></v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item two-line>
              <v-list-item-content>
                <v-list-item-title></v-list-item-title>
                <v-list-item-subtitle>{{ username }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>

            <v-list-item three-line>
              <v-list-item-content>
                <v-list-item-title></v-list-item-title>
                <v-list-item-subtitle>{{ email }}</v-list-item-subtitle>
                <v-list-item-subtitle v-if="email_verify == 1">()</v-list-item-subtitle>
                <v-list-item-subtitle v-else>()</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>

            <v-list-item four-line>
              <v-list-item-content>
                <v-list-item-title></v-list-item-title>
                <v-list-item-subtitle>{{ usertype }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>

        <v-col>
          <v-card class="ma-4 pa-12">
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title></v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item two-line>
              <v-list-item-content>
                <v-text-field
                  :value="mystate"
                  @change="callOnMyStateChanged"
                  type="text"
                  label=""
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>

            <v-list-item three-line>
              <v-list-item-content>
                <v-select
                  @change="callOnSexChanged"
                  :items="sexitems"
                  :value="sex"
                  item-text="label"
                  item-value="value"
                  label=""
                ></v-select>
              </v-list-item-content>
            </v-list-item>

            <v-list-item four-line>
              <v-list-item-content>
                <v-list-item-title>
                  <v-btn color="blue" class="white--text" @click="change_profile()"></v-btn>
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import sexdata from "@/helpers/sex";
import { mapState, mapMutations } from "vuex";
import { ChangeProfile } from "@/request/api";

export default {
  data() {
    return {
      sexitems: sexdata
    };
  },
  computed: {
    ...mapState({
      avatar: state => state.user.avatar,
      username: state => state.user.username,
      email: state => state.user.email,
      email_verify: state => state.user.email_verify,
      usertype: state => state.user.usertype,
      mystate: state => state.user.mystate,
      sex: state => state.user.sex
    })
  },
  methods: {
    //
    change_profile() {
      // if (!this.autograph) {
      //   this.$snackbar.warning("");
      // } else if (this.autograph == this.mystate) {
      //   this.$snackbar.warning("");
      // } else {
      ChangeProfile({
        mystate: this.mystate,
        sex: this.sex
      }).then(res => {
        if (res.code != 0) {
          this.$snackbar.error(res.message);
        } else {
          this.$snackbar.success(res.message);
        }
      });
    },

    callOnMyStateChanged(state) {
      this.updateMyState(state);
    },

    callOnSexChanged(sex) {
      this.updateSex(sex);
    },

    go_gravatar() {
      window.location.href = "https://gravatar.com";
    },

    ...mapMutations(["updateMyState", "updateSex"])
  }
};
</script>
